.container {
  margin: 0 20.78vw;
}
.new-nav {
  padding: 36px 0;
}
.news {
  width: 96px;
  font-family: Museo100-Regular;
  font-size: 36px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
  color: #888888;
  margin-right: 112px !important;
}
.news-btn {
  cursor: pointer;
  width: 148px;
  height: 38px;
  line-height: 38px;
  font-size: 14px;
  text-align: center;
  color: #888888;
  border-radius: 19px;
  background-color: #ffffff;
  border: solid 1px #eeeeee;
}
.news-btn.active {
  border: solid 1px #eeeeee;
  background-color: #333333;
  color: #eeeeee;
}
.news-card {
  padding: 5.12vw 0;
}
.news-card .left {
  margin: 0 0;
}
.news-card .left .inner {
  background-color: #1b358a;
  width: 18.23vw;
  height: 15.63vw;
}
.news-card .right {
  margin: 0 0;
  position: relative;
  font-family: SourceHanSansCN-Light;
  margin-left: 4.53vw;
}
.news-card .right a {
  font-size: 0.83vw;
  color: #888888;
}
.news-card .right .text-1 {
  margin-top: 0;
  height: 0.83vw;
  font-family: SourceHanSansCN-Light;
  font-size: 0.83vw;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0vw;
  color: #888888;
}
.news-card .right .text-2 {
  margin-top: 1.82vw;
  height: 1.51vw;
  font-family: SourceHanSansCN-Light;
  font-size: 1.56vw;
  font-weight: normal;
  font-stretch: normal;
  line-height: 1.56vw;
  letter-spacing: 0vw;
  color: #888888;
}
.news-card .right .text-3 {
  margin-top: 2.03vw;
  height: 2.34vw;
  font-family: SourceHanSansCN-Light;
  font-size: 0.73vw;
  font-weight: normal;
  font-stretch: normal;
  line-height: 1.56vw;
  letter-spacing: 0vw;
  color: #888888;
}
.news-card .right .bottom-btn {
  position: absolute;
  left: 0;
  bottom: 1.77vw;
  cursor: pointer;
}
.news-card .right .bottom-btn::after {
  content: "";
  background: url(data:image/png;base64,) no-repeat;
  display: inline-block;
  width: 22px;
  height: 22px;
  zoom: 0.6;
  margin-left: 11px;
  margin-bottom: -2px;
}
.news-page {
  padding: 4.11vw;
}
.news-page .page-item {
  cursor: pointer;
  margin: 0 20px;
  font-family: SourceHanSansCN-Light;
  font-size: 1.25vw;
  color: #888888;
}
.news-page .page-item.active {
  color: #333333;
}
.news-page::before {
  content: "";
  background: url(data:image/png;base64,) no-repeat;
  background-position: center;
  display: inline-block;
  width: 2vw;
  height: 2vw;
  margin-bottom: -2px;
}
.news-page::after {
  content: "";
  background: url(data:image/png;base64,) no-repeat;
  background-position: center;
  display: inline-block;
  width: 2vw;
  height: 2vw;
  margin-bottom: -2px;
}
.news-info {
  position: relative;
  color: #888888;
  font-family: SourceHanSansCN-Light;
}
.news-info .text-1 {
  font-size: 16px;
  margin-bottom: 35px;
}
.news-info .text-2 {
  font-size: 30px;
  margin-bottom: 39px;
}
.news-info .text-3 {
  font-size: 14px;
  margin-bottom: 35px;
}
.news-info .text-right {
  text-align: right;
}
.news-info .text-left {
  text-align: left;
}
.news-info .dis-ib {
  display: inline-block;
}
.news-info .return {
  margin-right: 192px;
  margin-left: 44px;
  width: 92px;
  height: 38px;
  background-color: #333333;
  border-radius: 19px;
  border: solid 1px #eeeeee;
  background: url(data:image/png;base64,) no-repeat;
  cursor: pointer;
}
.news-info .return:hover {
  background: url(data:image/png;base64,) no-repeat;
}
.news-info .return a {
  display: block;
  width: 92px;
  height: 38px;
}
.news-info .placeholder {
  margin: 53px 0;
  height: 445px;
  background-color: #1b358a;
}
@media (max-width: 768px) {
  .container {
    margin: 0 3.61vw;
  }
  .new-nav {
    padding: 10.19vw 0;
  }
  .navbar-default {
    background-color: #fff;
    border-color: #fff;
  }
  .news {
    display: none;
  }
  .news-btn {
    width: 21.3vw;
    height: 5.56vw;
    line-height: 5.56vw;
    border-radius: 2.78vw;
    border: 1px solid #eeeeee;
    font-size: 2.78vw;
    text-align: center;
    color: #888888;
    background-color: #ffffff;
  }
  .news-btn.active {
    border: solid 1px #eeeeee;
    background-color: #333333;
    color: #eeeeee;
  }
  .news-card {
    padding: 5.47vw 0;
  }
  .news-card.row-reverse {
    flex-direction: row-reverse;
  }
  .news-card .left {
    margin: 0;
  }
  .news-card .left .inner {
    width: 32.41vw;
    height: 27.78vw;
  }
  .news-card .right {
    margin: 0;
    flex-grow: 1;
    position: relative;
  }
  .news-card .right .text-1 {
    position: absolute;
    left: 0;
    bottom: 8.74vw;
    margin-bottom: 0vw;
    font-size: 2.22vw;
  }
  .news-card .right .text-2 {
    position: absolute;
    margin-top: 0;
    top: 0;
    font-size: 4.44vw;
    line-height: 4.67vw;
    margin-bottom: 0vw;
  }
  .news-card .right .text-3 {
    display: none;
  }
  .news-card .right .bottom-btn {
    position: absolute;
    left: 0;
    bottom: 0;
    font-size: 2.22vw;
  }
  .news-card .right .bottom-btn:after {
    content: "";
    background: url(data:image/png;base64,) no-repeat;
    display: inline-block;
    border-radius: 0.05vw;
    zoom: 0.4;
    margin-left: 5.01vw;
    margin-bottom: -0.2px;
  }
  .news-page {
    padding: 7.4vw;
  }
  .news-page .page-item {
    margin: 0 1.85vw;
    font-size: 2.22vw;
    color: #888888;
  }
  .news-page .page-item.active {
    color: #333333;
  }
  .news-page::before {
    content: "";
    background: url(data:image/png;base64,) no-repeat;
    background-position: center;
    display: inline-block;
    width: 40px;
    height: 22px;
    zoom: 0.6;
    margin-bottom: -2px;
  }
  .news-page::after {
    content: "";
    background: url(data:image/png;base64,) no-repeat;
    background-position: center;
    display: inline-block;
    width: 40px;
    height: 22px;
    zoom: 0.6;
    margin-bottom: -2px;
  }
  .news-info {
    position: relative;
    color: #888888;
    font-family: SourceHanSansCN-Light;
  }
  .news-info .col-md-5 {
    width: 100%;
  }
  .news-info .text-1 {
    color: #888888;
    width: 27.13vw;
    font-size: 2.78vw;
    line-height: 5.56vw;
    margin-bottom: 0;
  }
  .news-info .text-1 br {
    display: none;
  }
  .news-info .text-2 {
    display: none;
  }
  .news-info .text-3 {
    font-size: 2.22vw;
    margin-bottom: 8.06vw;
  }
  .news-info .text-right {
    text-align: left;
  }
  .news-info .text-left {
    text-align: left;
  }
  .news-info .dis-ib {
    display: inline-block;
  }
  .news-info .return {
    margin-right: 0;
    margin-left: 0;
    width: 12.04vw;
    height: 5.56vw;
    background-color: #ffffff;
    border-radius: 2.78vw;
    border: solid 0.09vw #eeeeee;
    background: url(data:image/png;base64,) no-repeat;
    background-size: cover;
    right: 4.91vw;
    position: absolute;
  }
  .news-info .return a {
    display: inline-block;
    width: 100%;
    height: 100%;
  }
  .news-info .placeholder {
    margin: 5.65vw 0 10.4vw;
    width: 90.65vw;
    height: 50.09vw;
    background-color: #1b358a;
  }
}
